  width: 11.25rem;
  margin-right: 0.65rem;
  padding: 0.65rem;
  text-align: left;
  font-size: 1rem;
  background-color: inherit;
  color: white;
  border: none;
}
#control-panel .dropdown .dropbtn:hover {
  background-color: #032541;
}
