  display: none;
  position: absolute;
  background-color: #032541;
  max-width: 11.25rem;
  width: 100%;
  z-index: 1;
  margin-top: 0.065px;
  border: 0.065px solid #032541;
  border-radius: 0.5rem;
}
#control-panel .dropdown .tag {
  width: 100%;
  display: flex;
  padding: 0.5rem;
  cursor: pointer;
  color: white;
}
#control-panel .dropdown .tag:hover {
  background-color: #043055;
}
#control-panel .dropdown .tag.highlight {
  color: #01B4E4;
}
#control-panel .favorite,
#control-panel .remove-all {
  width: 10.625rem;
  margin-right: 0.5rem;
  padding: 0.65rem;
  font-size: 1rem;
  text-align: left;
  background-color: inherit;
  color: white;
  border: none;
  border-radius: 0.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
#control-panel .favorite:hover,
#control-panel .remove-all:hover {
  background-color: #032541;
}

.movie {
  position: relative;
  width: 18.75rem;
  margin: 1rem;
  border-radius: 0.5rem;
  box-shadow: 0.013rem 0.25rem 0.5rem rgba(0, 0, 0, 0.1);
  display: flex;
  flex-wrap: nowrap;
  flex-direction: column;
  justify-content: space-between;
  align-items: stretch;
}
.movie img {
  width: 100%;
  height: -webkit-fill-available;
  border-radius: 1rem;
}
