  position: absolute;
  bottom: 0;
  width: 100%;
  padding: 0.5rem 1rem 0.5rem;
  display: flex;
  flex-direction: column;
  background-color: #e5f9ff;
  opacity: 0.8;
  color: #01B4E4;
  border-radius: 0 0 1rem 1rem;
}
.movie .movie-info h3 {
  margin: 0;
  font-weight: bold;
  font-size: 1rem;
}
@media screen and (min-width: 992px) {
  .movie .movie-info {
    display: none;
  }
