  position: absolute;
  top: 0.65rem;
  right: 0.65rem;
  width: 2.5rem;
  height: 2.5rem;
  font-size: 1.5rem;
  border: 0.065px solid #01B4E4;
  color: #01B4E4;
  background: #e5f9ff;
  opacity: 0.8;
  border-radius: 50%;
}
.movie .add-to-favorite.hidden,
.movie .remove-from-favorite.hidden {
  display: none;
}

.pagination {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: 0.65rem 2rem;
  color: white;
}
.pagination.hidden {
  display: none;
}
.pagination .page {
  cursor: pointer;
  padding: 1.5rem;
}
.pagination .page .disabled {
  cursor: not-allowed;
  color: grey;
}
.pagination .current {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 4.5rem;
  height: 4.5rem;
  padding: 0.65rem 1.5rem;
  border: 0.5rem solid #01B4E4;
  border-radius: 50%;
  color: #01B4E4;
  font-size: 1.5rem;
  font-weight: 600;
